import { element } from 'protractor';
import { PerguntaMultEscolhaComponent } from './../pergunta-mult-escolha/pergunta-mult-escolha.component';
import { PerguntaRadioComponent } from './../pergunta-radio/pergunta-radio.component';
import { PerguntaTextoComponent } from './../pergunta-texto/pergunta-texto.component';
import { ServerServices } from './../../shared/serverServices.service';
import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactory, ComponentFactoryResolver } from '@angular/core';

@Component({
  selector: 'app-editar-formulario',
  templateUrl: './editar-formulario.component.html',
  styleUrls: ['./editar-formulario.component.css'],
  providers:[ServerServices]
})
export class EditarFormularioComponent implements OnInit {

  constructor(private service :ServerServices, private resolver: ComponentFactoryResolver) { }

  @ViewChild('editar', {read:ViewContainerRef}) entry: ViewContainerRef
  controlador: any
  perguntaTexto = this.resolver.resolveComponentFactory(PerguntaTextoComponent)
  perguntaRadio = this.resolver.resolveComponentFactory(PerguntaRadioComponent)
  perguntaCheck = this.resolver.resolveComponentFactory(PerguntaMultEscolhaComponent)

  ngOnInit() {
  this.service.servico("GET", "/form", null).subscribe((data:any)=>{
    console.log(data)


    data.forEach(element => {
      if (element.tipo == "1"){
        this.controlador = this.entry.createComponent(this.perguntaTexto)
        this.controlador.instance.tituloPergunta == element.texto
      }else if (element.tipo == "2"){
        this.controlador = this.entry.createComponent(this.perguntaRadio)
        this.percorreArray(element.pergunta)

      }else if (element.tipo == "3"){
        this.controlador = this.entry.createComponent(this.perguntaCheck)
        console.log(element)

        this.percorreArray(element.pergunta)
      }

    });
  })

  }


  percorreArray(subComp: any){
    subComp.forEach(componente => {
      console.log(componente.valor)

      if (componente.id == 1){

        this.controlador.instance.componentes[0].valor=componente.valor
      }else{
        console.log("ASDASD")
        this.controlador.instance.adicionar()
        this.controlador.instance.componentes[componente.id-1].valor= componente.valor
      }
    });

  }

}



