import { EditarFormularioComponent } from './formulario/editar-formulario/editar-formulario.component';
import { NgModule } from '@angular/core';

import { FormularioComponent } from './formulario/formulario.component';
import { AppComponent } from './app.component';
import { TopoComponent } from './topo/topo.component';
import { LoginComponent } from './login/login.component';
import { HomePageComponent } from './home-page/home-page.component';
import { CriarContaComponent } from './criar-conta/criar-conta.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule } from '@angular/router';
import { ROUTES } from './app.routes';
import {NgxMaskModule} from 'ngx-mask'
import { MeusAlunosComponent } from './home-page/meus-alunos/meus-alunos.component';
import { AdicionarAlunoComponent } from './home-page/adicionar-aluno/adicionar-aluno.component';
import { PerguntaTextoComponent } from './formulario/pergunta-texto/pergunta-texto.component';
import { PerguntaRadioComponent } from './formulario/pergunta-radio/pergunta-radio.component';
import { PerguntaMultEscolhaComponent } from './formulario/pergunta-mult-escolha/pergunta-mult-escolha.component';
import { InicioComponent } from './inicio/inicio.component';
import { TopoInicioComponent } from './inicio/topo-inicio/topo-inicio.component';
import { QuemsomosComponent } from './inicio/quemsomos/quemsomos.component';
import { SaibamaisComponent } from './inicio/saibamais/saibamais.component';
import { CriarTreinoComponent } from './home-page/meus-alunos/criar-treino/criar-treino.component';
import { EscolherMembroComponent } from './home-page/meus-alunos/criar-treino/escolher-membro/escolher-membro.component';
import { EscolherMusculoComponent } from './home-page/meus-alunos/criar-treino/escolher-musculo/escolher-musculo.component';
import { EscolherExercicioComponent } from './home-page/meus-alunos/criar-treino/escolher-exercicio/escolher-exercicio.component';


@NgModule({
  declarations: [
    AppComponent,
    TopoComponent,
    LoginComponent,
    HomePageComponent,
    CriarContaComponent,
    MeusAlunosComponent,
    AdicionarAlunoComponent,
    FormularioComponent,
    PerguntaTextoComponent,
    PerguntaRadioComponent,
    PerguntaMultEscolhaComponent,
    InicioComponent,
    TopoInicioComponent,
    QuemsomosComponent,
    SaibamaisComponent,
    CriarTreinoComponent,
    EscolherMembroComponent,
    EscolherMusculoComponent,
    EscolherExercicioComponent,
    EditarFormularioComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    NgxMaskModule.forRoot(),
    RouterModule.forRoot(ROUTES),

  ],
  providers: [],
  bootstrap: [AppComponent],
  entryComponents:[PerguntaTextoComponent, PerguntaRadioComponent, PerguntaMultEscolhaComponent]
})
export class AppModule { }



