import { Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { HomePageComponent } from './home-page/home-page.component';
import { CriarContaComponent } from './criar-conta/criar-conta.component';
import { MeusAlunosComponent } from './home-page/meus-alunos/meus-alunos.component';
import { AdicionarAlunoComponent } from './home-page/adicionar-aluno/adicionar-aluno.component';
import { InicioComponent } from './inicio/inicio.component';
import { QuemsomosComponent } from './inicio/quemsomos/quemsomos.component';
import { SaibamaisComponent } from './inicio/saibamais/saibamais.component';

export const ROUTES: Routes = [

    {path: '', component: InicioComponent},
    {path: 'quemsomos', component: QuemsomosComponent},
    {path: 'saibamais', component: SaibamaisComponent},


    {path: 'meusAlunos', component: MeusAlunosComponent},
    {path: 'addAluno', component: AdicionarAlunoComponent},
    {path: 'criarConta', component: CriarContaComponent},
    {path: 'home', component: HomePageComponent},
    {path: 'login', component: LoginComponent}
]


