import { ServerServices } from './serverServices.service';
import { AbstractControl, FormControl } from '@angular/forms';

export class Valida {

    constructor ( private service: ServerServices) { }

    static validaSenha ( AC : AbstractControl) {
        let senha = AC.get('senha').value;
        let confirmaSenha = AC.get('confirmaSenha').value;

        if (senha !== confirmaSenha) {
            AC.get('confirmaSenha').setErrors( { MatchPassword: true } )
        }
        else {
            return null
        }
    }

}



