import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-pergunta-radio',
  templateUrl: './pergunta-radio.component.html',
  styleUrls: ['./pergunta-radio.component.css']
})
export class PerguntaRadioComponent implements OnInit {
  tituloPergunta: string;
  contador: number =1;
  componentes : {id:number, valor:string}[] = [{"id":1, "valor":""}]
  constructor() { }

  ngOnInit() {
  }

  deletar(valorIndex:any){
    let objeto = this.componentes.find(x => x.id == valorIndex)
    this.componentes.splice(this.componentes.indexOf(objeto), 1)
  }

  adicionar(){
    this.contador +=1;
    this.componentes.push({"id":this.contador, "valor":""})
  }

}



