import { Headers } from '@angular/http';
import { ServerServices } from './../shared/serverServices.service';
import { Usuario } from './../shared/usuario.model';
import { Component, OnInit } from '@angular/core';

import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router'
// import { Usuario } from '../shared/usuario.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[Usuario, ServerServices]
})
export class LoginComponent implements OnInit {

  form: FormGroup;
  email: string;
  password: string;
  cnpj: string;

  public user: Usuario;

  constructor(formBuilder: FormBuilder, private router: Router, private usuario: Usuario, private servico:ServerServices) {

    this.form = formBuilder.group({

      email: ['', [
        Validators.required,
        Validators.email
      ]],
      password: ['', [Validators.required]]
    });

   }

  ngOnInit() {

    window.localStorage.clear()

  }

  logar(){
    this.usuario.email = this.email;
    this.usuario.password = this.password
    this.servico.servico("POST", "/user/login", this.usuario).subscribe(
      (data:any)=>{
        let a = window.localStorage.setItem('nomePersonal', data.nome)
        let b = window.localStorage.setItem('userid', data.userid)
        this.router.navigate(['/home'])
    },

      (error: any) =>{
        alert("ATENÇÃO: " + error.mensagem)
      });

  }

  criarConta(){
    this.router.navigate(['/criarConta'])
  }

}



