import { ServerServices } from './../shared/serverServices.service';
import { PerguntaMultEscolhaComponent } from './pergunta-mult-escolha/pergunta-mult-escolha.component';
import { PerguntaRadioComponent } from './pergunta-radio/pergunta-radio.component';
import { PerguntaTextoComponent } from './pergunta-texto/pergunta-texto.component';
import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';

@Component({
  selector: 'app-formulario',
  templateUrl: './formulario.component.html',
  styleUrls: ['./formulario.component.css'],
  providers:[ServerServices]
})
export class FormularioComponent implements OnInit {
  selecao:any;
  referencias: {id:number, tipo:string, referencia:any} = {id:1, tipo:'String', referencia:0};
  componentes : {id:number, tipo:string, referencia:any}[] = [{id:0, tipo:'asd', referencia:0}]
  contador:number = 0;
  filhoJ : {}[] =[{}] ;


@ViewChild('entrada', {read:ViewContainerRef}) entry: ViewContainerRef
  constructor(private resolver:   ComponentFactoryResolver, private servico:ServerServices) { }
    opcao = [
      {'valor':1, 'tipo':'Texto'},
      {'valor':2, 'tipo':'Escolha'},
      {'valor':3, 'tipo':'Multipla Escolha'}
    ]

    adicionar(){
      let  perguntaTexto = this.resolver.resolveComponentFactory(PerguntaTextoComponent);
      let  perguntaRadio = this.resolver.resolveComponentFactory(PerguntaRadioComponent);
      let  perguntaMultEscolha = this.resolver.resolveComponentFactory(PerguntaMultEscolhaComponent);
      this.contador +=1;
      this.referencias.id = this.contador
        console.log(this.selecao )
      if(this.selecao == 1){
        this.componentes.push({"id":this.contador,
        "tipo":"Texto", "referencia":this.entry.createComponent(perguntaTexto)});
        console.log(this.componentes)
      }else if(this.selecao == 2){
        this.componentes.push({"id":this.contador,
        "tipo":"Radio", "referencia":this.entry.createComponent(perguntaRadio)});
        console.log(this.componentes)

      }else if(this.selecao == 3){
        this.componentes.push({"id":this.contador,
        "tipo":"CheckBox", "referencia":this.entry.createComponent(perguntaMultEscolha)});
        console.log(this.componentes)

      }
      console.log(this.componentes)
    }

    serializar(){
      this.componentes.forEach(x => {
        if (x.tipo == "Texto"){
          this.filhoJ.push({"tipo":"1", "pergunta":x.referencia.instance.tituloPergunta}) ;
        }else if(x.tipo == "Radio"){
          this.filhoJ.push({"tipo":"2", "pergunta": x.referencia.instance.radios})
        }else if (x.tipo == "CheckBox"){
          this.filhoJ.push({"tipo":"3", "pergunta":x.referencia.instance.check})
        }
      });
      console.log(this.filhoJ)
      this.servico.servico("POST", "/form", this.filhoJ).subscribe((data:any) =>{

      })
    }

  ngOnInit() {


  }

}



