import { ServerServices } from './../shared/serverServices.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-topo',
  templateUrl: './topo.component.html',
  styleUrls: ['./topo.component.css'],
  providers: [ServerServices]
})
export class TopoComponent implements OnInit {

  constructor(private service: ServerServices, private router: Router) {

  }

  ngOnInit() {
  }

  logout() {
    window.localStorage.clear()

    this.service.servico('DELETE', 'me/logout', null).subscribe(
      (res: any) => {
        console.log('RESPOSTA DO LOGOUT', res)
        this.router.navigate([''])
      },
      (error: any) => {
        console.log('DEU RUIM NO LOGOUT', error)
      }
    )

  }

}



