import { Http, Headers, HttpModule } from '@angular/http';
// import { Usuario } from './usuario.model';
import { Injectable } from '@angular/core';

import 'rxjs/add/operator/toPromise'
import 'rxjs/add/operator/map'
import { Observable } from 'rxjs/Observable';
import { RequestOptions } from '@angular/http/src/base_request_options';
import { Usuario } from './usuario.model';
import { environment } from '../../environments/environment';


@Injectable()
export class ServerServices {

    private url: any = environment.enderecoBaseApi;

    constructor(private http: Http){

    }

    public servico(method: string, caminho: string, params: any): Observable<any>{

        // this.url = 'http://localhost:3000'
        // this.url = 'http://trainow-api.herokuapp.com'

        const header = new Headers();
        header.append('Content-type', 'application/json');
        header.append('x-auth', localStorage.getItem('x-auth'))

        this.url += caminho;

        const response = Observable.create(observer => {
            switch (method) {
                case 'GET':
                    if (params != null){
                        this.concatenarUrl(params);
                    }
                    return this.http.get(this.url, {headers:header}).subscribe(
                        (res:any)=> {
                            if (res._body == ""){
                                observer.next(this.trataRetornoSucesso(res));
                            }else{
                                observer.next(this.trataRetornoSucesso(res.json()));
                            }
                        },
                        error =>{
                            console.log('AQUI É PRA DAR RUIM')
                            observer.error(this.trataRetornoErro(error));
                        }
                    );

                case 'POST':
                    JSON.stringify(params);
                    console.log(this.http)
                    return this.http.post(this.url, params, {headers:header}).subscribe(
                        (res:any)=> {
                            if (caminho == "/user/login"){
                                window.localStorage.setItem('x-auth', res.headers.get('x-auth'));
                            }
                            observer.next(this.trataRetornoSucesso(res.json()));
                        },
                        error =>{
                            observer.error(this.trataRetornoErro(error));
                        }
                    );


                case 'PUT':
                    JSON.stringify(params);
                    return this.http.put(this.url, params, {headers:header}).subscribe(
                        (res:any)=> {
                            observer.next(this.trataRetornoSucesso(res));
                        },
                        error =>{
                            observer.error(this.trataRetornoErro(error));
                        }
                    );

                case 'DELETE':
                    JSON.stringify(params);
                    return this.http.delete(`${this.url}/${params.id}`, {headers:header}).subscribe(
                        (res:any)=> {
                            observer.next(this.trataRetornoSucesso(res));
                        },
                        error =>{
                            observer.error(this.trataRetornoErro(error));
                        }
                    );
            }
        });
        return response;
    }

    private trataRetornoSucesso(response: any): any{

        if(response){
            console.log('Log do trata retorno sucesso ', response)
            return response;
        }
        return null;
    }

    private trataRetornoErro(erro: any): string{

        try {
            let error: any = JSON.parse(erro._body);
            let returnMessage: any = null;

            if (error !== null) {
                returnMessage = error
            }
            return returnMessage;
        }
        catch(e) {
            return "Ocorreu um Erro Inesperado";
        }
    }

    concatenarUrl(params){
        this.url +="?";
        for (const key of Object.keys(params)){
            if (Object.keys(params)[0] == key){
                this.url += key.toString();
                this.url +="=" + params[key];
            }else{
                this.url +="&" + key.toString();
                this.url += "=" + params[key];
            }
        }
    }

}


