import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-pergunta-mult-escolha',
  templateUrl: './pergunta-mult-escolha.component.html',
  styleUrls: ['./pergunta-mult-escolha.component.css']
})
export class PerguntaMultEscolhaComponent implements OnInit {
  tituloPergunta: string;
  contador: number = 1;
  componentes : {id:number, valor:string}[] = [{"id": this.contador, "valor":""}]
  constructor() { }

  deletar(valorIndex:any){
    console.log(valorIndex)
    let objeto = this.componentes.find(x => x.id == valorIndex);
    console.log(this.componentes)
    console.log(objeto)
   this.componentes.splice(this.componentes.indexOf(objeto), 1)

  }

  ngOnInit() {
  }

  adicionar(){
    this.contador+=1

    this.componentes.push({"id":this.contador, "valor":""})
  }

}



