import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Aluno } from '../../shared/aluno.model';
import { ServerServices } from '../../shared/serverServices.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-adicionar-aluno',
  templateUrl: './adicionar-aluno.component.html',
  styleUrls: ['./adicionar-aluno.component.css'],
  providers: [ServerServices]
})
export class AdicionarAlunoComponent implements OnInit {

  public aluno: Aluno;
  public form: FormGroup


  nome:string;
  idade: number;
  email:string;
  telefone:string;
  dataNasc: string;


  constructor(private service: ServerServices, private router: Router, private formBuilder: FormBuilder) {

    this.form = formBuilder.group({
      nome: ['', [
        Validators.required,
        Validators.pattern(/[^0-9\.\,\"\?\!\;\:\#\$\%\&\(\)\*\+\-\/\<\>\=\@\[\]\\\^\_\{\}\|\~]+/)
      ]],
      dataNasc: ['', [
        Validators.required,
        Validators.pattern(/^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/)
      ]],
      email: ['', [
        Validators.required,
        Validators.email
      ]],
      telefone: ['', [
        Validators.required,
        Validators.maxLength(11),
        Validators.minLength(11),
        Validators.pattern(/^(0|[1-9][0-9]*)$/)
      ]],
    });


  }

  ngOnInit() {
    console.log(window.localStorage.getItem('userid'))
  }


  cadastrar() {

    this.aluno = new Aluno;

    this.aluno.nome = this.nome;
    this.aluno.email = this.email;
    this.aluno.telefone = this.telefone;
    this.aluno.dataNascimento = this.dataNasc;
    this.aluno.personal = window.localStorage.getItem('userid')

    this.service.servico('POST','/aluno', this.aluno).subscribe(
      (data: any) => {
        alert('Aluno cadastrado com sucesso')
        this.router.navigate(['/meusAlunos'])
      },
      (error: any) => {
        alert(error)
      }
    )

  }

}



