import { Valida } from './../shared/valida';
import { ServerServices } from './../shared/serverServices.service';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Usuario } from './../shared/usuario.model';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'

@Component({
  selector: 'app-criar-conta',
  templateUrl: './criar-conta.component.html',
  styleUrls: ['./criar-conta.component.css'],
  providers:[Usuario, ServerServices]
})
export class CriarContaComponent implements OnInit {

  public form: FormGroup;
  public personal: Usuario;
  public nome: string;
  public userid: string;
  public dataNasc: string;
  public email: string;
  public numeroCref: string;
  public digitoCref: string;
  public estadoCref: string;
  public telefone: number;
  public senha: string;
  public confirmaSenha: string;

  constructor(formBuilder: FormBuilder, private router: Router, private service:ServerServices) {
    this.form = formBuilder.group({
      nome: ['', [
        Validators.required,
        Validators.pattern(/[^0-9\.\,\"\?\!\;\:\#\$\%\&\(\)\*\+\-\/\<\>\=\@\[\]\\\^\_\{\}\|\~]+/)
      ]],
      userid: ['', [
        Validators.required
      ]],
      email: ['', [
        Validators.required,
        Validators.email
      ]],
      numeroCref:['', [
        Validators.required,
        Validators.minLength(6),
        Validators.maxLength(6)
      ]],
      digitoCref:['', [
        Validators.required,
        this.selecionouAlgo
      ]],
      estadoCref:['', [
        Validators.required,
        this.selecionouAlgo
      ]],
      telefone: ['', [
        Validators.required,
        Validators.maxLength(11),
        Validators.minLength(11),
        Validators.pattern(/^(0|[1-9][0-9]*)$/)
      ]],
      dataNasc: ['', [
        Validators.required,
        Validators.pattern(/^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)?\d{2})$/)
      ]],
      senha:['', [
        Validators.required
      ]],
      confirmaSenha:['', [
        Validators.required
      ]]
    }, {
      validator: Valida.validaSenha
    });

  }
  ngOnInit() {
  }

  selecionouAlgo(input: AbstractControl) {
    if (input.value !== 0) {
      return null
    }
    else {
      return {erro: true}
    }
  }

  cadastrar(){

    this.personal = new Usuario;

    this.personal.nome = this.nome;
    this.personal.userid = this.userid;
    this.personal.email = this.email;
    this.personal.registroPersonal = this.numeroCref + '-' + this.digitoCref + '/' + this.estadoCref;
    this.personal.telefone = this.telefone;
    this.personal.dataNascimento = this.dataNasc
    this.personal.password = this.senha;
    this.personal.typeUser ="personal";

    this.service.servico("POST", "/user", this.personal).subscribe(
      (data:any) =>{
        console.log(this.personal)
        alert('Conta criada com sucesso')
        this.router.navigate(['/login'])
      },
      (error:any) => {
        alert(error)
      }
    );


  }

}



